import React from "react"
import Welcome from "../components/welcome"
import Projects from "../components/projects"
import Blog from "../components/blog"
import MasterLayout from "../components/master-layout"
import ContentLayout from "../components/content-layout"
import blogStyles from "../components/blog.module.scss"
import Img from "gatsby-image"
import { FaBook } from "react-icons/fa"
import { Link, graphql, useStaticQuery } from "gatsby"
import { IoIosArrowForward } from "react-icons/io"

import Newsletter from "../components/newsletter"

export const indexHeadingStyle = {
  marginBottom: ".3rem",
  fontWeight: "900",
  // color: "rgba(92, 37, 127, 1)",
}

export const indexSubHeadingStyle = {
  marginBottom: "1.5rem",
  lineHeight: "1.5",
}

export const contentBoxButton = {
  textDecoration: "none",
  color: "#fff",
  fontWeight: "400",
  textTransform: "uppercase",
  fontSize: "1rem",
}

export const subscribeSectionStyle = {
  textAlign: "center",
  backgroundColor: "grey",
}

const IndexPage = () => {

  const data = useStaticQuery(graphql`
  query {
    allMdx(filter: { fields: { contentType: { eq: "posts" } } }, limit: 5) {
      edges {
        node {
          frontmatter {
            title
            tags
            date(formatString: "MMMM DD, YYYY")
            author
            thumbnail {
              childImageSharp {
                fluid(quality: 50) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
          excerpt
          timeToRead
          fields {
            slug
          }
        }
      }
    }
  }
`)



  return (
    <MasterLayout>
     
      <Welcome />
     
      <ContentLayout>
        <h1 style={indexHeadingStyle}>
          {" "}
          <FaBook /> From my blog{" "}
        </h1>
        <p style={indexSubHeadingStyle}>See the recent on my article list</p>
        {/* <Blog /> */}
        <div className="editByMe">
        <div className={blogStyles.blogWrapper}>
      {data.allMdx.edges.map(edge => {
        return (
          <Link to={`/blog/${edge.node.fields.slug}`}>
            <article className={blogStyles.blogItem}>
              <figure>
                <Img
                  fluid={edge.node.frontmatter.thumbnail.childImageSharp.fluid}
                  alt="blog-thumbnail"
                  className={blogStyles.image}
                />
              </figure>

              <div className={blogStyles.content}>
                <h2>{edge.node.frontmatter.title}</h2>
                <div className={blogStyles.metaData}>
                  <date>{edge.node.frontmatter.date}</date>
                  <span>&nbsp;/&nbsp;{edge.node.timeToRead} min read</span>
                  <ul>
                    {edge.node.frontmatter.tags.map(tag => {
                      return <li>{tag}</li>
                    })}
                  </ul>
                </div>
              </div>
            </article>
          </Link>
        )
      })}
    </div>
    </div>
        <Link to="/blog" className="box-button" style={contentBoxButton}>
          View all Articles
        </Link>
      </ContentLayout>
      <ContentLayout bgcolor="#EAEFD3">
        <h1 style={indexHeadingStyle}>Projects</h1>
        <p style={indexSubHeadingStyle}>
          Checkout some of my projects with case study
        </p>
        <Projects />
        <Link to="/projects" className="box-button" style={contentBoxButton}>
          View All Projects
        </Link>
      </ContentLayout>

      <ContentLayout bgcolor="#e8e8e8">
        <Newsletter />
      </ContentLayout>
    </MasterLayout>
  )
}

export default IndexPage
